a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline
}

html {
    line-height: 1
}

ol, ul {
    list-style: none
}

table {
    border-collapse: collapse;
    border-spacing: 0
}

caption, td, th {
    text-align: left;
    font-weight: 400;
    vertical-align: middle
}

blockquote, q {
    quotes: none
}

blockquote:after, blockquote:before, q:after, q:before {
    content: "";
    content: none
}

a img {
    border: none
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
    display: block
}

* {
    box-sizing: border-box
}

html {
    font-size: 87.5%;
    line-height: 1.57143em;
    font-size: 14px;
    line-height: 1.6em;
    -webkit-text-size-adjust: 100%
}

body {
    background: #fcfcfc;
    color: #545454;
    text-rendering: optimizeLegibility;
    font-family: AvenirNext-Regular
}

a {
    color: #de4c4f;
    text-decoration: none
}

h1 {
    font-family: AvenirNext-Medium;
    color: #333;
    font-size: 1.6em;
    line-height: 1.3em;
    margin-bottom: .78571em
}

h2 {
    font-size: 1.3em;
    margin-bottom: .62857em
}

h2, h3 {
    font-family: AvenirNext-Medium;
    color: #333;
    line-height: 1em
}

h3 {
    font-size: 1.15em;
    margin-bottom: .47143em
}

p {
    margin-bottom: 1.57143em;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto
}

hr {
    height: 1px;
    border: 0;
    background-color: #dedede;
    margin: -1px auto 1.57143em
}

ol, ul {
    margin-bottom: .31429em
}

ol ol, ol ul, ul ol, ul ul {
    margin-bottom: 0
}

ol li:before {
    content: counter(ol) ".";
    counter-increment: ol;
    color: #e06e73;
    text-align: right;
    display: inline-block;
    min-width: 1em;
    margin-right: .5em
}

b, strong {
    font-family: AvenirNext-Bold
}

em, i {
    font-family: AvenirNext-Italic
}

code {
    font-family: Menlo-Regular
}

.text-overflow-ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap
}

.sf_code_attr-name, .sf_code_builtin, .sf_code_char, .sf_code_inserted, .sf_code_selector, .sf_code_string {
    color: #d33905
}

.sf_code_cdata, .sf_code_comment, .sf_code_doctype, .sf_code_prolog {
    color: #838383
}

.sf_code_atrule, .sf_code_attr-value, .sf_code_boolean, .sf_co de_class, .sf_code_class-name, .sf_code_function, .sf_code_important, .sf_code_interpolation, .sf_code_keyword, .sf_code_number, .sf_code_regex, .sf_code_rule, .sf_code_variable {
    color: #0e73a2
}

.sf_code_constant, .sf_code_deleted, .sf_code_property, .sf_code_symbol, .sf_ code_tag {
    color: #1b00ce
}

.sf_code_entity, .sf_code_macro, .sf_code_operat or, .sf_code_url {
    color: #920448
}

.note-wrapper {
    max-width: 46em;
    margin: 0 auto;
    padding: 1.57143em 3.14286em
}

.note-wrapper.spo tlight-preview {
    overflow-x: hidden
}

u {
    text-decoration: none;
    background-image: linear-gradient(180deg, transparent 50%, #e06e73 0);
    background-repeat: repeat-x;
    background-size: 2px 2px;
    background-position: 0 1.05em
}

s {
    color: #878787
}

p {
    margin-bottom: .1em
}

hr {
    margin-bottom: .7em;
    margin-top: .7em
}

ul li {
    text-indent: -.35em
}

ul li:before {
    content: "â€¢";
    color: #e06e73;
    display: inline-block;
    margin-right: .3em
}

ul ul {
    margin-left: 1.25714em
}

ol li {
    text-indent: -1.45em
}

ol ol {
    margin-left: 1.25714em
}

blockquote {
    display: block;
    margin-left: -1em;
    padding-left: .8em;
    border-left: .2em solid #e06e73
}

.todo-list ul {
    margin-left: 1.88571em
}

.todo-list li {
    text-indent: -1.75em
}

.todo-list li:before {
    content: "";
    display: static;
    margin-right: 0
}

.todo-checkbox {
    text-indent: -1.7em
}

.todo-checkbox svg {
    margin-right: .3em;
    position: relative;
    top: .2em
}

.todo-checkbox svg #check {
    display: none
}

.todo-checkbox.todo-checked #check {
    display: inline
}

.todo-checkbox.todo-checked .todo-text {
    text-decoration: line-through;
    color: #878787
}

.code-inline {
    display: inline;
    padding: .2em .5em
}

.code-inline, .code-multiline {
    background: #fff;
    border: 1px solid #dedede;
    font-size: .9em
}

.code-multiline {
    display: block;
    padding: .7em 1em;
    overflow-x: auto
}

.hashtag {
    display: inline-block;
    background: #b8bfc2;
    padding: 0 .5em;
    border-radius: 1em;
    text-indent: 0
}

.hashtag, .hashtag a {
    color: #fff
}

.address a {
    color: #545454;
    background-image: linear-gradient(180deg, transparent 50%, #0da35e 0);
    background-repeat: repeat-x;
    background-size: 2px 2px;
    background-position: 0 1.05em
}

.address svg {
    margin-right: .2em
}

.address svg, .color-preview {
    position: relative;
    top: .2em;
    display: inline-block
}

.color-preview {
    width: 1em;
    height: 1em;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 50%;
    margin-right: .1em;
    white-space: nowrap
}

.color-code {
    margin-right: .2em;
    font-family: Menlo-Regular;
    font-size: .9em
}

.color-hash {
    opacity: .4
}

.ordered-list-number {
    color: #e06e73;
    text-align: right;
    display: inline-block;
    min-width: 1em
}

.arrow svg {
    position: relative;
    top: .08em;
    display: inline-block;
    margin-right: .15em;
    margin-left: .15em
}

.arrow svg #rod {
    stroke: #545454
}

.arrow svg #point {
    fill: #545454
}

mark {
    color: inherit;
    display: inline;
    padding: .2em .5em;
    background-color: #fcffc0
}

img {
    max-width: 100%;
    height: auto
}
